import axios from 'axios'
const url = 'https://shielded-ridge-40989.herokuapp.com/api/persons'

const getAll = () => {
    return axios.get(url)
  }
  
  const create = newObject => {
    return axios.post(url, newObject)
  }
  const deleteId = (idea) => {
    return axios.delete(`https://shielded-ridge-40989.herokuapp.com/api/persons/${idea}`)
  }

  
  export default { 
    getAll: getAll, 
    create: create, 
    deleteId: deleteId
  }